import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";
/**
 * @param {idAppl}
 * @result list menu
 * descrtion set toi datatable
 */


export const GetMenuListAction = (type, idApp) => {
  return(dispatch) => {
    loadingStatus(true)
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/GetApplTasks?IdAppl=${idApp}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type, payload: data.data });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  }
};
