import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";


export const setFormUserAction = (formType, formValue) => {
  return { type: "ADD_USER", formType, formValue };
};

/**
 * @param {}
 * @result List of users
 * Description get api users
 */

export const GetUsersAPIAction = (type) => {
  return (dispatch) => {
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/GetUsers`
      )
      .then((res) => {
        let data = res.data
        if(data.isSuccess) {
          let tmpArr = [];
          data.data.map((value, index) => {

            var roleNameTmp =
              " " +
              value.roles
                .map(function (i) {
                  return i.roleName;
                })
                .join(" || ");
            tmpArr.push({
              idUser: value.idUser,
              lastLogin: value.lastLogin,
              fullName: value.fullName,
              strStatus: value.strStatus,
              username: value.username,
              password: value.password,
              email: value.email,
              firstName: value.firstName,
              middleName: value.middleName,
              lastName: value.lastName,
              address: value.address,
              phoneNumber: value.phoneNumber,
              mobileNumber: value.mobileNumber,
              fileImage: value.fileImage,
              roleName :roleNameTmp
            });
          });
           dispatch({type, payload: tmpArr});
        }else{
           Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

export const GetUserAPIByIdUserAction = (idUser) => {

  return (dispatch) => {
    loadingStatus(true);
    dispatch({ type: "RESET_FORM_USER" });
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/GetUser?IdUser=${idUser}`
      )
      .then((result) => {
        loadingStatus(false);
        let data = result.data;
        if (data.isSuccess) {
          dispatch(setFormUserAction("Username", data.data.username));
          dispatch(setFormUserAction("IdUser", data.data.idUser));
          dispatch(setFormUserAction("Email", data.data.email));
          dispatch(setFormUserAction("FirstName", data.data.firstName));
          dispatch(setFormUserAction("MiddleName", data.data.middleName));
          dispatch(setFormUserAction("LastName", data.data.lastName));
          dispatch(setFormUserAction("Address", data.data.address));
          dispatch(setFormUserAction("PhoneNumber", data.data.phoneNumber));
          dispatch(setFormUserAction("MobileNumber", data.data.mobileNumber));
           var rolesTmpArr = [];
           data.data.roles.map((v, i) => {
             rolesTmpArr.push({
               value: v.idRole,
               label: v.roleName,
             });
           });

           dispatch(setFormUserAction("Roles", rolesTmpArr));
            dispatch({ type: "MODAL_USER", payload: true });

        } else {
           Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  }
}

export const AddUserAPIAction = (data) => {
   let idUser = sessionStorage.getItem("idUser");
  return(dispatch) => {
    loadingStatus(true)
    let dataNewValue = {...data}
    let tmpRoles = [];
    data.Roles.map((v) => {
      tmpRoles.push({
        IdRole: v.value,
      });
    });
    dataNewValue["Roles"] = tmpRoles;
    data = dataNewValue;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/AddUser?IdUser=${idUser}`,
        data
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil menambah data`, "success");
          dispatch({ type: "MODAL_USER", payload: false });
          dispatch({ type: "RESET_FORM_USER" });
          dispatch(GetUsersAPIAction("GET_USER_LIST"));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  }
}

export const SetUserInActiveAction = (idUserParam, fullName) => {
   let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {
     Swal.fire({
       title: "Apakah anda yakin?",
       text: `Akan Non aktifkan ${fullName}`,
       icon: "warning",
       showCancelButton: true,
       confirmButtonColor: "#3085d6",
       cancelButtonColor: "#d33",
       confirmButtonText: "Ya, Akan Nonaktif",
     }).then((result) => {
       if (result.isConfirmed) {
         http
           .post(
             `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/SetUserInActive?IdUser=${idUser}&SetIdUser=${idUserParam}`
           )
           .then((res) => {
             let result = res.data;
             if (result.isSuccess) {
               Swal.fire(
                 `Sukses`,
                 `Data ${fullName} berhasil nonaktif`,
                 `success`
               );
               dispatch({ type: "RESET_FORM_USER" });
               dispatch(GetUsersAPIAction("GET_USER_LIST"));
             } else {
               Swal.fire("Gagal", `${result.returnMessage}`, "error");
             }
           });
       }
     });
  }
};

export  const SetUserActiveAction = (idUserParam, fullName) => {
  let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {
    Swal.fire({
      title: "Apakah anda yakin?",
      text: `Akan Mengaktifkan ${fullName}`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Ya, Akan Aktifkan",
    }).then((result) => {
      if (result.isConfirmed) {
        http
          .post(
            `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/SetUserActive?IdUser=${idUser}&SetIdUser=${idUserParam}`
          )
          .then((res) => {
            let result = res.data;
            if (result.isSuccess) {
              Swal.fire(`Sukses`, `Data ${fullName} berhasil Aktif`, `success`);
              dispatch({ type: "RESET_FORM_USER" });
              dispatch(GetUsersAPIAction("GET_USER_LIST"));
            } else {
              Swal.fire("Gagal", `${result.returnMessage}`, "error");
            }
          });
      }
    });
  };
};


export const UpdateUserAPIAction = (data) => {
  let idUser = sessionStorage.getItem("idUser");
  return (dispatch) => {
    loadingStatus(true);
    let dataNewValue = { ...data };
    let tmpRoles = [];
    data.Roles.map((v) => {
      tmpRoles.push({
        IdRole: v.value,
      });
    });
    dataNewValue["Roles"] = tmpRoles;
    data = dataNewValue;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/EditUser?IdUser=${idUser}`,
        data
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil merubah data`, "success");
          dispatch({ type: "MODAL_USER", payload: false });
          dispatch({ type: "RESET_FORM_USER" });
          dispatch(GetUsersAPIAction("GET_USER_LIST"));
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

