import { http } from "src/common/helper";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";




export const setDataComboProv = (type) => {
  return (dispatch) => {
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetProvinsis`
      )
      .then((res) => {
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type, payload: data.data });
        } else {

          Swal.fire("Gagal", `${data.returnMessage}`, "error");

        }
      });
  };
};

/**
 *
 * @param {idProv} type int
 * @returns list Of Kabupaten
 */

export const setDataDropdownKabupatenAction = (type, idProv) => {
  return (dispatch) => {
    loadingStatus(false);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetKabupatens?IdProvinsi=${idProv}`
      )
      .then((res) => {
        let data = res.data;
        if(data.isSuccess){
           dispatch({ type, payload: data.data })
        }else{
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
          dispatch({ type: "DATA_CMB_KAB_REST" });
        }
      });
  };
};

