/**
 * @param {}
 * @returns list of roles
 */
export const SetDropdownRolesAction = (type) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/GetRoles`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          let tmpArr = [];
          data.data.map((v, i) => {
            tmpArr.push({
              value: v.idRole,
              label: v.roleName
            });
          })
          dispatch({ type, payload: tmpArr });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};
/**
 * @param {}
 * @returns list of Jabatan
 */
export const SetDropdownPgwKecamatansByKecamatanAction = (type, idKec) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetPegawaiKecamatansByKecamatan?IdKecamatan=${idKec}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          let tmpArr = [];
          tmpArr.push({
            value: 0,
            label : " -- Pilih -- "
          })
          data.data.map((v, i) => {
            tmpArr.push({
              value: v.idJabatan,
              label: v.jabatan,
            });
          });
          dispatch({ type, payload: tmpArr });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};



