import { http } from "src/common/helper";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";

export const SetPegawaiChangedAction = (formType, formValue) => {
  return { type: "SET_FIRST_ADD_MONEV", formType, formValue };
};
export const GetPegawiKecamatanAction = (idPegawai) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetPegawaiKecamatan?IdPegawai=${idPegawai}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          console.log(data.data.idUser);
          dispatch(SetPegawaiChangedAction("IdPegawai", data.data.idUser));
          dispatch(SetPegawaiChangedAction("FullName", data.data.fullName));
          dispatch(
            SetPegawaiChangedAction("MobileNumber", data.data.mobileNumber)
          );
          dispatch(SetPegawaiChangedAction("Email", data.data.email));
          dispatch(
            SetPegawaiChangedAction("OfficeAddress", data.data.officeAddress)
          );
        } else {
          Swal.fire("Gagal", `${data.ReturnMessage}`, "error");
        }
      });
  };
};
