
import {http} from '../../helper/http';
import Swal from 'sweetalert2';
import { useHistory } from "react-router-dom";
import serviceName from 'src/common/helper/serviceName';
export const setFormLogin = (formType, formValue) => {

    return {type: 'login', formType, formValue}
}

export const setFormForgot = (formType, formValue) => {
  return { type: "forgot", formType, formValue };
}

export const ResetPasswordAction = (data) => {
  return(dispatch) => {
    http.post(
      `${process.env.REACT_APP_ROOT_API}${serviceName.svcSettingApps}/ResetPassword?UsernamOrEmail=${data.UsernameOrEmailForgot}`
    ).then(res => {
      let data = res.data;
      if(data.isSuccess){
         Swal.fire("Berhasil", `Anda Berhasil reset password`, "success");
         dispatch({ type: "MODAL_FORGOT_PASSWORD", payload:true});
      }else{
         Swal.fire("Gagal", `${data.returnMessage}`, "error");
      }
    });
  }
}

export const apiLogin = (iData) => {

    return (dispatch) => {
          http.get(`${process.env.REACT_APP_ROOT_API}UserApps/Login?UsernameOrEmail=${iData.UsernameOrEmail}&Password=${iData.Password}`

        ).then((res) => {

            let data = res.data;
            if (data.isSuccess) {
                sessionStorage.setItem('isSuccessLgn', true);
                sessionStorage.setItem('idUser', res.data.data.idUser);
                Swal.fire('Berhasil', `Selamat datang!!!`, 'success').then(() => window.location = '/')
            } else {
                Swal.fire('Gagal', `${data.returnMessage}`, 'error');
            }
        })
    }
}
