import { http } from "src/common/helper";
import serviceName from "src/common/helper/serviceName";
import swal from "sweetalert";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";

/**
 *
 * @param {idKab} type int
 * @returns list Of Kabupaten
 */
export const GeKecamatanByIdKabupatenAction = (type, idKab) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetKecamatans?IdKabupaten=${idKab}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch({ type, payload: data.data });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

/**
 *
 * @param {} type
 * @returns get onchange each komponen form
 */

export const setFormKecAction = (formType, formValue) => {
  return { type: "ADD_KECAMATAN", formType, formValue };
};

/**
 * @param{data} objcet formkecamat
 * @returns
 */

export const prosesKecamatanAddAction = (data) => {
  return (dispatch) => {
    loadingStatus(true);
    let param = `?IdKabupaten=${data.IdKabupaten}&Kode=${data.Kode}&Nama=${data.Nama}&Alamat=${data.Alamat}&Longitude=${data.Longitude}&Latitude=${data.Latitude}`;
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/AddKecamatan${param}`
      )
      .then((res) => {
        let data = res.data;
        if (data.isSuccess) {
          Swal.fire("Berhasil", `Anda telah berhasil menambah data`, "success");
          dispatch({ type: "MODAL_KEC", payload: false });
          dispatch({ type: "RESET_FORM_KEC" });
          dispatch(
            GeKecamatanByIdKabupatenAction("GET_KECAMATANS", data.IdKabupaten)
          );
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};

/**
 * @param{idKec} int idKec
 * @returns Kecamatan by Id
 */
export const GetKecamatanByIdKecamatanAction = (idKecamata) => {
  return (dispatch) => {
    loadingStatus(false);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetKecamatan?IdKecamatan=${idKecamata}`
      )
      .then((res) => {
        let data = res.data;
        if (data.isSuccess) {
          dispatch(setFormKecAction("IdKecamatan",data.data.idKecamatan));
          dispatch(setFormKecAction("Kode",data.data.kode));
          dispatch(setFormKecAction("Nama",data.data.nama));
          dispatch(setFormKecAction("Alamat",data.data.alamat));
          dispatch(setFormKecAction("IdKabupaten",data.data.idKabupaten));
          dispatch(setFormKecAction("Longitude",data.data.longitude));
          dispatch(setFormKecAction("Latitude",data.data.latitude));
          dispatch({ type: "MODAL_KEC",payload:true });
        } else {
          Swal.fire("Gagal", `${data.ReturnMessage}`, "error");
        }
      });
  };
};

/**
 * @param{objeForm Kecamata} object Kecamatan form
 * @returns
 */
export const prosesKecamatanEditAction = (data)=>{

  return (dispatch) => {
    loadingStatus(false);
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/EditKecamatan`,
        data
      )
      .then((res) => {
        let data = res.data;
        if (res.data.isSuccess) {

          Swal.fire("Berhasil", `Anda telah berhasil merubah data`, "success");
          dispatch({ type: "MODAL_KEC", payload: false });
          dispatch(
            GeKecamatanByIdKabupatenAction("GET_KABUPATENS", data.IdKabupaten)
          );

        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
}

/**
 * @param{idKecamatan} int idKecamatan
 * @returns
 */
export const ProsesDeleteKecamatanAction = (idKecamatan, idKabupaten, nama) => {
  console.log(idKecamatan)
  return(dispatch) =>{
      Swal.fire({
        title: "Apakah anda yakin?",
        text: `Akan menghapus Kecamatan ${nama}`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Ya, Akan Menghapus",
      }).then((result) => {
        if (result.isConfirmed) {
          http
            .post(
              `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/DeleteKecamatan?IdKecamatan=${idKecamatan}`
            )
            .then((res) => {
              let result = res.data;
              if (result.isSuccess) {
                Swal.fire(`Sukses`,`Data ${nama} berhasil dihapus`,`success`);
                 dispatch({ type: "RESET_FORM_KAB" });
                 dispatch(GeKecamatanByIdKabupatenAction("GET_KECAMATANS", idKabupaten));
              } else {
                Swal.fire("Gagal", `${result.returnMessage}`, "error");
              }
            });

        }
      });
  }
}
